@import "https://fonts.googleapis.com/css2?family=Raleway:wght@500;600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Raleway, sans-serif;
  position: relative;
}

img {
  width: 100%;
  display: block;
}

.container {
  grid-gap: 10px;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.img-container {
  position: relative;
}

.content {
  width: 100%;
  color: #fff;
  opacity: 0;
  background: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: opacity 1s;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
}

.img-container:hover .content {
  opacity: 1;
  background: tomato;
}

.zoom-text {
  width: 80px;
  height: 80px;
  text-transform: uppercase;
  background: #deb887;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  transition: transform .2s linear;
  display: flex;
}

.content h2 {
  padding: 15px 0;
}

.zoom-text:hover {
  cursor: zoom-in;
  transform: scale(1.1);
}

.image-view {
  height: 100vh;
  width: 100%;
  background: #000c;
  display: none;
  position: fixed;
  top: 0;
}

.image-box {
  width: 70vw;
  height: 80vh;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#prev-btn, #next-btn {
  width: 0;
  height: 0;
  cursor: pointer;
  border-top: 20px solid #0000;
  border-bottom: 20px solid #0000;
  transition: all .5s;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

#prev-btn {
  border-right: 40px solid gray;
  left: -50px;
}

#next-btn {
  border-left: 40px solid gray;
  right: -50px;
}

#next-btn:hover, #prev-btn:hover {
  opacity: .6;
}

@media (width <= 992px) {
  .container {
    grid-template-columns: 1fr;
  }

  .image-box {
    height: 55vh;
  }
}

@media (width <= 678px) {
  .image-box {
    height: 40vh;
  }
}

@media (width <= 400px) {
  .image-box {
    height: 25vh;
  }
}

